import React, {useState, useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url, $http} from "config";
import {NotificationContainer, NotificationManager} from 'react-notifications';

const columnData = [
  {id: "id", align: false, disablePadding: true, label: "ID"},
  {id: "notification", align: true, disablePadding: false, label: "Notification"},
  {id: "created_by", align: true, disablePadding: false, label: "Created By"},
  {id: "customer", align: true, disablePadding: false, label: "Customer"},
  {id: "created_date", align: true, disablePadding: false, label: "Created Date"},
  {id: "actions", align: true, numeric: 'right', disablePadding: false, label: "Actions"}
];

const NotificationTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                    <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

NotificationTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired
};


let NotificationTableToolbar = (props) => {
  const History = useHistory();

  const NewNotification = () => {
    History.push('add-new');
  }

  const {SearchNotification} = props;

  return (
    <Toolbar className="table-header">
      <div className="title">
        <Typography variant="h6">Notifications</Typography>
      </div>
      <div className="spacer">
          <div className="col-md-3 col-lg-3 col-sx-6 col-6 ml-auto">
              <SearchBox placeholder="Search ..."
                onChange={(event) => SearchNotification(event.target.value)}
              />
          </div>
      </div>
      <div className="actions">
        <Tooltip title="New Notification">
          <IconButton aria-label="New Notification" onClick={()=>NewNotification()}>
              <PlaylistPlayIcon/>
          </IconButton>
        </Tooltip>
      </div>
    </Toolbar>
  );
};

const NotTable = () => {
  
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const [selected, setSelected] = useState([]);
  const [loader, setLoader] = useState(false);
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [tabledata, setTabledata] = useState([]);
  const [Notifications, setNotifications] = useState([]);

  useEffect(() => {
    init_Data();
  }, []);

  const init_Data = () => {
    $http.get(base_url + "notification/getNotification")
    .then((response) => {
      setTabledata(response.data.notifications);
      setNotifications(response.data.notifications);
      setLoader(true);
    });
  }

  const handleRequestSort = (event, property) => {

    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? Notifications.sort((a, b) => (b[property] < a[property] ? -1 : 1))
      : Notifications.sort((a, b) => (a[property] < b[property] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };


  const handleKeyDown = (event, id) => {
    if (keycode(event) === "space") {
      handleClick(event, id);
    }
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const onSelectNotification = (data, index) => {
    $http.post(base_url + "notification/deleteNotification", {id: data})
    .then((response) => {
    init_Data();
    NotificationManager.info("You removed selected notification");
    })
  }

  const SearchNotification = (data) => {
    setNotifications(
      tabledata.filter(function (item) {
        return (item.notification.toLowerCase().includes(data.toLowerCase()));
      })
    );
  }

  return (
    
        <div>
              <NotificationTableToolbar SearchNotification={SearchNotification}/>
          {
            loader ? (
              <div className="flex-auto">
                <div className="table-responsive-material">
                  <Table className="">
                    <NotificationTableHead
                      order={order}
                      orderBy={orderBy}
                      onRequestSort={handleRequestSort}
                    />
                    <TableBody>
                      {Notifications.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                          <TableRow
                            hover
                            key={n.id}
                            // onClick={event => handleClick(event, n.id)}
                            onKeyDown={event => handleKeyDown(event, n.id)}
                            tabIndex={-1}
                          >
                            <TableCell>{n.id}</TableCell>
                            <TableCell>{n.notification}</TableCell>
                            <TableCell>{n.first_name}</TableCell>
                            <TableCell>{n.company_name}</TableCell>
                            <TableCell>{n.created_at}</TableCell>
                            <TableCell className="text-right">
                              <CardEditMenu key={n.id} selectedData={n.id} 
                                onSelectRow={onSelectNotification}/>
                            </TableCell>
                          </TableRow>
                        )}
                    </TableBody>
                    <TableFooter>
                      <TableRow>
                        <TablePagination
                          count={Notifications.length}
                          rowsPerPage={rowsPerPage}
                          page={page}
                          onChangePage={handleChangePage}
                          onChangeRowsPerPage={handleChangeRowsPerPage}
                        />
                      </TableRow>
                    </TableFooter>
                  </Table>
                </div>
                <NotificationContainer/>
              </div>
            ):(
              <div className="loader-view"
                      style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                  <CircularProgress/>
              </div>        
            )
          }
        </div>
  );
};

export default NotTable;
